h1,
h2,
h3,
p {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

body {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.SearchBarDiv {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 10vh;
}

/* the form itself */
.SearchBarDiv > form {
  min-width: 50%;
  display: flex;
  justify-content: space-around;
}

#searchBar {
  flex: 1;
  max-width: 70%;
  padding: 0.5rem;
  border-radius: 8px;
  border: solid 2px black;
  min-width: 300px;
  transition: transform 0.3s ease;
}

#submitBtn {
  cursor: pointer;
  border: solid 2px black;
  background-color: #fff;
  border-radius: 6px;
  padding: 0;
  margin: 0;
  min-height: 2.25rem;
  min-width: 2.25rem;
  display: flex;
  justify-content: center;
  transition:
    transform 0.3s ease,
    background-color 0.3s ease;
}

#submitBtn:active {
  background-color: grey;
}

#submitBtn:hover,
#searchBar:hover {
  transform: translate(0, -2px);
}

.weatherInfoContainer:hover {
  transform: translate(-50%, -61%);
}

#submitBtn > img {
  filter: invert(100%);
  padding: 0;
  margin: 0;
}

/* weather info container */
.iconImage {
  width: 6rem;
}

.weatherInfoContainer {
  position: absolute;
  min-width: 420px;
  max-width: 530px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -60%);
  padding: 2rem;
  border-radius: 2rem;
  border: solid 2px black;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 3fr;
  gap: 1rem;
  transition:
    transform 0.3s ease,
    background-color 0.3s ease;
}

.weatherInfoContainer > .cityName {
  grid-column: 1/-1;
  text-align: center;
}

.leftPart {
  display: grid;
  align-items: top;
  grid-template-rows: 30px 140px;
  grid-template-columns: 1fr 2fr;
}

.leftPart > p {
  display: flex;
  font-weight: bold;
  justify-content: center;
  align-items: end;
  font-size: 1.2rem;
  grid-column: 1/-1;
}

.leftPart > img {
  align-self: center;
  width: 120px;
  padding: 0.2rem;
}

.leftPart > div {
  font-size: 1.35rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

span.temperature {
  display: flex;
}

.rightPart {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 5px;
}

.rightPart > div {
  border: solid 2px black;
  font-weight: bold;
  padding: 0.8rem 1.5rem;
  display: flex;
  justify-content: space-between;
  border-radius: 1rem;
}

.conditions {
  text-align: center;
}

/* loading */
#loading{
  font-size: 2rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}`, "",{"version":3,"sources":["webpack://./src/general.css"],"names":[],"mappings":"AAAA;;;;;EAKE,SAAS;EACT,UAAU;EACV,yCAAyC;AAC3C;;AAEA;EACE,aAAa;EACb,aAAa;EACb,sBAAsB;AACxB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,YAAY;AACd;;AAEA,oBAAoB;AACpB;EACE,cAAc;EACd,aAAa;EACb,6BAA6B;AAC/B;;AAEA;EACE,OAAO;EACP,cAAc;EACd,eAAe;EACf,kBAAkB;EAClB,uBAAuB;EACvB,gBAAgB;EAChB,+BAA+B;AACjC;;AAEA;EACE,eAAe;EACf,uBAAuB;EACvB,sBAAsB;EACtB,kBAAkB;EAClB,UAAU;EACV,SAAS;EACT,mBAAmB;EACnB,kBAAkB;EAClB,aAAa;EACb,uBAAuB;EACvB;;8BAE4B;AAC9B;;AAEA;EACE,sBAAsB;AACxB;;AAEA;;EAEE,6BAA6B;AAC/B;;AAEA;EACE,gCAAgC;AAClC;;AAEA;EACE,oBAAoB;EACpB,UAAU;EACV,SAAS;AACX;;AAEA,2BAA2B;AAC3B;EACE,WAAW;AACb;;AAEA;EACE,kBAAkB;EAClB,gBAAgB;EAChB,gBAAgB;EAChB,QAAQ;EACR,SAAS;EACT,gCAAgC;EAChC,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,aAAa;EACb,8BAA8B;EAC9B,2BAA2B;EAC3B,SAAS;EACT;;8BAE4B;AAC9B;;AAEA;EACE,iBAAiB;EACjB,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,gBAAgB;EAChB,8BAA8B;EAC9B,8BAA8B;AAChC;;AAEA;EACE,aAAa;EACb,iBAAiB;EACjB,uBAAuB;EACvB,gBAAgB;EAChB,iBAAiB;EACjB,iBAAiB;AACnB;;AAEA;EACE,kBAAkB;EAClB,YAAY;EACZ,eAAe;AACjB;;AAEA;EACE,kBAAkB;EAClB,aAAa;EACb,sBAAsB;EACtB,mBAAmB;EACnB,uBAAuB;AACzB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,uBAAuB;EACvB,QAAQ;AACV;;AAEA;EACE,uBAAuB;EACvB,iBAAiB;EACjB,sBAAsB;EACtB,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;AACrB;;AAEA;EACE,kBAAkB;AACpB;;AAEA,YAAY;AACZ;EACE,eAAe;EACf,iBAAiB;EACjB,aAAa;EACb,mBAAmB;EACnB,uBAAuB;AACzB","sourcesContent":["body,\nh1,\nh2,\nh3,\np {\n  margin: 0;\n  padding: 0;\n  font-family: Arial, Helvetica, sans-serif;\n}\n\nbody {\n  height: 100vh;\n  display: flex;\n  flex-direction: column;\n}\n\n.SearchBarDiv {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  height: 10vh;\n}\n\n/* the form itself */\n.SearchBarDiv > form {\n  min-width: 50%;\n  display: flex;\n  justify-content: space-around;\n}\n\n#searchBar {\n  flex: 1;\n  max-width: 70%;\n  padding: 0.5rem;\n  border-radius: 8px;\n  border: solid 2px black;\n  min-width: 300px;\n  t